@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=REM:wght@100;200;300;400;500;600;700;800;900&display=swap');

html {
    @apply relative scroll-smooth;
}


body {
    font-size: 15px;
    @apply font-body overflow-x-hidden text-gray-900;
}

// Fix event bubbling on iOS devices
@supports (-webkit-overflow-scrolling: touch) {
    body {
        cursor: pointer;
    }
}

input,
textarea,
button,
select,
a {
    -webkit-tap-highlight-color: transparent;
}

h1,h2,h3,h4,h5,h6,p {
    @apply font-normal;
}



#swiper_one {
    .swiper-pagination-bullet {
        background-color: #059669;
    }
}

#swiper_Two {
    .swiper-pagination-bullet {
        height: 10px;
        width: 10px;
        background-color: #9333ea;
    }
}

.swiper-button-prev {
    --swiper-navigation-size: 20px;
}

.swiper-button-next {
    --swiper-navigation-size: 20px;
}